nav{
    background: rgba(179, 179, 179, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1.2rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 1.5rem;
    display: flex;
    color: var(--color-light);
    font-size: 1.3rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}

@media screen and (max-width: 600px){
    nav{
        background: rgba(179, 179, 179, 0.3);
        width: max-content;
        display: block;
        padding: 0.5rem 0.6rem;
        z-index: 2;
        position: fixed;
        left: 50%;
        transform: translate(-50%);
        bottom: 2rem;
        display: flex;
        gap: 0.5rem;
        backdrop-filter: blur(15px);
    }
    
    nav a{
        background: transparent;
        padding: 1.5rem;
        display: flex;
        color: var(--color-light);
        font-size: 1.0rem;
    }
    
    nav a:hover{
        background: rgba(0, 0, 0, 0.3);
    }
    
    nav a.active {
        background: var(--color-primary);
        color: var(--color-bg);
    }
}